	module.exports = {"data":{"markdownRemark":{"html":"<p> Here's this via Audio Activism, for folks in the Triangle, NC area. Looks to be a great night of the up-and-coming Triangle blogging community:</p>\n<blockquote class=\"large\">\n<p>\"On Tuesday, November 15 from 7-9 p.m we’re having a Triangle Blogger’s Bash at Durham’s American Tobacco Historic District. The event will include a walking tour of WUNC’s new studios, a catered reception, talking about podcasting, and drinks at Tyler’s Speakeasy next door. Everyone is invited. Come to learn about blogging, podcasting, and meet your neighbors who have those cool blogs you read all the time.\"</p>\n</blockquote>\n<p>Read it: <a href=\"http://www.audioactivism.org/2005/11/03/triangle-blogger%e2%80%99s-bash-durham-november-15/\">Triangle Blogger’s Bash, Durham, November 15</a></p>\n<p>(Found on:<a href=\"http://www.audioactivism.org\">Audio Activism</a>.)</p>","frontmatter":{"created_at":"November 12, 2005","path":"/2005/11/12/triangle-bloggers-bash-durham-november-15","title":"Triangle Blogger's Bash, Durham, November 15","tags":["blogging","nc"]}}},"pathContext":{}}

